.listaDeServicos {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    padding: 10px;
    row-gap: 5px;
    border-radius: 5px;
    background-color: #FFF;
}

.titulo {
    font-weight: bold;
}

.sub-titulo {
    font-size: 14;
    font-weight: bold;
    width: 100%;
    height: 30px;
    line-height: 30px;
}

.listaDeServicos-item {
    display: flex;
    flex-direction: column;
    width: 99%;
    height: auto;
    background-color: #EEE;
    padding-right: 10px;
    padding-left: 10px;
    border-radius: 5px;
}

.listaDeServicos-item-info {
    display: flex;
    width: 99%;
    height: 25px;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.listaDeServicos-sub-item {
    width: 33.33%;
    color: #474747;
    font-size: 16px;
}

.listaDeServicos-sub-item-quantidade {
    width: 33.33%;
    color: #474747;
    text-align: right;
    font-size: 16px;
}

.listaDeServicos-sub-item-data {
    width: 33.33%;
    font-weight: bold;
    color: #474747;
}

.select-lista {
    width: 100%;
    height: 30px;
    border-width: 0px;
    border-radius: 5px;
    background-color: #FFF;
    color: #474747;
}

.select-lista:focus {
    border-width: 1px;
    border-style: solid;
    outline: 0px;
    border-color: #4CBC73;
}

.select-lista-label {
    width: 100%;
    font-size: 14px;
}