.PainelComposicao-painel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    row-gap: 10px;
    width: 70%;
    padding: 10px;
    border-radius: 8px;
    background-color: #FFF;
}

.PainelComposicao-titulo {
    font-size: 14px;
    font-weight: bold;
}

.PainelComposicao-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    gap: 5px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    
}

.PainelComposicao-container::-webkit-scrollbar {
    height: 8px;
    background-color: #E0E6EA;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.PainelComposicao-container::-webkit-scrollbar-thumb {
    background-color: #007AD9;
    border-radius: 20px;
    width: 10px;
}

.Categoria-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80%;
    width: 200px;
    padding: 10px;
    border-radius: 5px;
    border-style: ridge;
    border-width: 1px;
    box-shadow:  5px 5px 10px #f2f2f2,
             -5px -5px 10px #ffffff;
}

.Categoria-titulo {
    font-size: 12px;
    font-weight: bold;
    margin-bottom: 10px;
}

.Subprocesso-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 90%;
}

.Subprocesso-texto {
    font-size: 10px;
}

.Texto-lista-vazia {
    font-size: 14px;
    width: 100%;
    height: 100%;
    text-align: center;
}