.container {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    row-gap: 10px;
    padding: 20px;
    background-color: #F1F2F2;
}

.Grafico-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: 210px;
    padding: 20px;
    row-gap: 10px;
    border-radius: 8px;
    background-color: #F1F2F2;
}

.Grafico-titulo {
    font-size: 14px;
    font-weight: bold;
}

.Grafico-grupo-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    padding: 10px;
    row-gap: 10px;
    border-radius: 8px;
    background-color: #FFF;
}